* {
  box-sizing: border-box;
}

body {
  width: 100%;
  font-size: 14px;
  margin: 0;
  background-color: rgba(216, 126, 156, 0.25);
}

#root {
  width: 100%;
}

body{
    margin:0 auto;
    width:100%;

}
header {
  height: 8em;
  display: flex;
  align-items: center;
  border-bottom: 1px solid;
  justify-content: left;
  background: #6441a5; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #bd98db,
    #6441a5
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    left,
    #b62424,
    rgb(229, 160, 160)
  );
  background: linear-gradient(
    to right,
    #b62424,
    rgb(229, 160, 160)
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

header > img {
  height: 75%;
  margin: 1em 0em 1em 0.5em;
}

header > p {
  font-family: VT323, monospace;
  color: whitesmoke;
  font-size: 2rem;
  margin-left: 1.5rem;
}

.meme {
  position: relative;
  width: 100%;
  margin-top: 1.2em;
  text-align: center;
}

.meme > img {
  width: 95%;
  max-width: 40em;
  max-height: 40em;
  margin-bottom: 1.5em;
  background: cover;
  background-repeat: no-repeat;
}

.meme > h2 {
  position: absolute;
  max-width: 36em;
  text-align: center;
  left: 49%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin: 0.3em 0;
  padding: 0 0px;
  font-family: impact, sans-serif;
  text-transform: uppercase;
  color: white;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}

.meme > .bottom {
  bottom: 0.9em;
}

.meme > .top {
  top: 0.1em;
}

.meme-form {
  width: 90%;
  margin: 1.4em 0em 1em 1.4em;
  display: inline-block;
  justify-content: space-between;
}

.meme-form > input {
  border: 1.5px solid;
  border-radius: 5px;
  margin: 0.4em;
  display: block;
  width: 40%;
  min-width: 20em;
  height: 3em;
}

.meme-form > button {
  height: 2.1em;
  width: 8em;
  margin-top: 1em;
  margin-left: 0.1em;
  border: 3px rgb(221, 210, 210) solid;
  font-family: VT323, monospace;
  font-size: 20px;
  letter-spacing: 1.5px;
  color: white;
  background: rgb(138, 32, 32);
}
.meme-form > button:hover {
  background: rgba(138, 32, 32, 0.8);
}
.meme-form > input::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  font-family: VT323, monospace;
  font-size: 1.3em;
  text-align: left;
  padding-left: 5px;
}
.meme-form > input::-moz-placeholder {
  /* Firefox 19+ */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
.meme-form > input:-ms-input-placeholder {
  /* IE 10+ */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
.meme-form > input:-moz-placeholder {
  /* Firefox 18- */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}

